import React, { useState, useEffect } from 'react';
import { Switch, Route, useLocation, Redirect } from 'react-router-dom';
import {
  Container, 
  CssBaseline,
  ThemeProvider,
  createTheme,
  Paper,
  Typography,
  CircularProgress
} from '@material-ui/core';
import { 
  // Material UI icons that are actually used are imported here
} from '@material-ui/icons';
import Header from './components/Header';

// Import our pages
import HomePage from './pages/HomePage';
import ExercisePage from './pages/ExercisePage';
import HistoryPage from './pages/HistoryPage';
import ProfilePage from './pages/ProfilePage';
import LoginPage from './pages/LoginPage';
import RegisterPage from './pages/RegisterPage';
import CameraTestPage from './pages/CameraTestPage';
import HomeWorkoutsPage from './pages/HomeWorkoutsPage';
import GymWorkoutsPage from './pages/GymWorkoutsPage';
import PerformanceAnalysisPage from './pages/PerformanceAnalysisPage';
import YogaPage from './pages/YogaPage';

// API base URL - make sure this matches your backend
const API_URL = 'http://localhost:5000';

// Create a theme
const theme = createTheme({
  palette: {
    type: 'dark',
    primary: {
      main: '#2196f3',
    },
    secondary: {
      main: '#4caf50',
    },
    background: {
      default: '#121212',
      paper: '#1e1e1e',
    },
    text: {
      primary: '#ffffff',
      secondary: 'rgba(255, 255, 255, 0.7)',
    }
  },
  typography: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    h1: {
      fontSize: '2.5rem',
      fontWeight: 500,
    },
    h2: {
      fontSize: '2rem',
      fontWeight: 500,
    },
  },
  overrides: {
    MuiAppBar: {
      colorPrimary: {
        backgroundColor: '#1e1e1e',
      }
    },
    MuiPaper: {
      root: {
        backgroundColor: '#1e1e1e',
      }
    },
    MuiCard: {
      root: {
        backgroundColor: '#1e1e1e',
      }
    }
  }
});

// Protected route component
const ProtectedRoute = ({ component: Component, isLoggedIn, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      isLoggedIn ? (
        <Component {...props} />
      ) : (
        <Redirect to={{ pathname: "/login", state: { from: props.location } }} />
      )
    }
  />
);

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Check if user is logged in
    const checkAuth = async () => {
      try {
        setLoading(true);
        const response = await fetch(`${API_URL}/api/check-auth`, {
          credentials: 'include'
        });
        
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const data = await response.json();
        setIsLoggedIn(data.authenticated);
      } catch (error) {
        console.error('Error checking authentication:', error);
        setIsLoggedIn(false);
      } finally {
        setLoading(false);
      }
    };
    
    checkAuth();
  }, []);

  if (loading) {
    return (
      <Container style={{ textAlign: 'center', padding: '100px 0' }}>
        <CircularProgress size={60} />
        <Typography variant="h6" style={{ marginTop: '20px' }}>
          Loading application...
        </Typography>
      </Container>
    );
  }

  // Simplified app content that uses Header component
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div style={{ display: 'flex', flexDirection: 'column', minHeight: '100vh', backgroundColor: '#121212' }}>
        <Header isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn} />
        
        <Container style={{ flex: 1, paddingTop: '20px', paddingBottom: '20px' }}>
          <Switch>
            {/* Public routes */}
            <Route path="/login" render={(props) => <LoginPage {...props} setIsLoggedIn={setIsLoggedIn} />} />
            <Route path="/register" component={RegisterPage} />
            <Route path="/camera_test" component={CameraTestPage} />
            
            {/* Protected routes */}
            <ProtectedRoute exact path="/" component={HomePage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/dashboard" component={ProfilePage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/home-workouts" component={HomeWorkoutsPage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/gym-workouts" component={GymWorkoutsPage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/yoga" component={YogaPage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/exercise/:name" component={ExercisePage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/performance/:exerciseId" component={PerformanceAnalysisPage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/history" component={HistoryPage} isLoggedIn={isLoggedIn} />
            <ProtectedRoute path="/profile" component={ProfilePage} isLoggedIn={isLoggedIn} />
            
            {/* Redirect based on auth status */}
            <Redirect to={isLoggedIn ? "/" : "/login"} />
          </Switch>
        </Container>
        
        <Paper style={{ padding: '20px', marginTop: 'auto', backgroundColor: '#1e1e1e', color: 'rgba(255, 255, 255, 0.7)' }} component="footer" square variant="outlined">
          <Typography variant="body2" align="center">
            © {new Date().getFullYear()} AIFitTrack. All rights reserved.
          </Typography>
        </Paper>
      </div>
    </ThemeProvider>
  );
}

export default App;