import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import {
  Typography,
  Container,
  Paper,
  TextField,
  Button,
  Box,
  CircularProgress,
  Divider,
  Card,
  CardContent,
  Stepper,
  Step,
  StepLabel,
  Fade,
  Grow,
  Zoom
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { 
  PersonAdd, 
  Email, 
  AccountCircle, 
  LockOutlined, 
  CheckCircle, 
  FitnessCenter 
} from '@material-ui/icons';
import Alert from '../components/Alert';

// API base URL
const API_URL = 'http://localhost:5000';

// Custom styles with hover effects
const useStyles = makeStyles((theme) => ({
  registerContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    minHeight: 'calc(100vh - 184px)',
    padding: theme.spacing(3, 0),
  },
  registerCard: {
    borderRadius: 12,
    boxShadow: '0 8px 40px rgba(0, 0, 0, 0.12)',
    overflow: 'hidden',
    transition: 'transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out',
    '&:hover': {
      transform: 'translateY(-8px)',
      boxShadow: '0 16px 70px rgba(0, 0, 0, 0.2)',
    },
  },
  header: {
    background: 'linear-gradient(to right, #4caf50, #8bc34a)',
    padding: theme.spacing(4),
    color: 'white',
    textAlign: 'center',
  },
  form: {
    padding: theme.spacing(4),
  },
  textField: {
    '& .MuiOutlinedInput-root': {
      transition: 'all 0.3s ease',
      '&:hover fieldset': {
        borderColor: theme.palette.primary.main,
      },
      '&.Mui-focused fieldset': {
        borderColor: theme.palette.primary.main,
        boxShadow: '0 0 0 2px rgba(33, 150, 243, 0.2)',
      },
    },
  },
  submitButton: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(2),
    height: 56,
    borderRadius: 28,
    background: 'linear-gradient(to right, #4caf50, #8bc34a)',
    boxShadow: '0 4px 10px rgba(76, 175, 80, 0.3)',
    transition: 'all 0.3s ease',
    textTransform: 'none',
    fontWeight: 600,
    fontSize: '1.1rem',
    '&:hover': {
      background: 'linear-gradient(to right, #43a047, #7cb342)',
      boxShadow: '0 6px 15px rgba(76, 175, 80, 0.4)',
      transform: 'translateY(-2px)',
    },
  },
  loginLink: {
    color: theme.palette.primary.main,
    textDecoration: 'none',
    fontWeight: 500,
    position: 'relative',
    padding: '2px 0',
    transition: 'all 0.3s ease',
    '&:after': {
      content: '""',
      position: 'absolute',
      width: '0%',
      height: '2px',
      bottom: '0',
      left: '50%',
      transform: 'translateX(-50%)',
      backgroundColor: theme.palette.primary.main,
      transition: 'all 0.3s ease',
    },
    '&:hover': {
      color: theme.palette.primary.dark,
      '&:after': {
        width: '100%',
      },
    },
  },
  logo: {
    fontSize: 60,
    color: 'white',
    background: 'rgba(255, 255, 255, 0.2)',
    padding: 12,
    borderRadius: '50%',
    marginBottom: theme.spacing(2),
  },
  stepper: {
    backgroundColor: 'transparent',
    padding: theme.spacing(3, 0),
  },
  stepIcon: {
    '&$completed': {
      color: '#4caf50',
    },
    '&$active': {
      color: '#4caf50',
    },
  },
  stepLabel: {
    transition: 'all 0.3s ease',
    '&:hover': {
      color: '#4caf50',
    },
  },
  active: {},
  completed: {},
}));

function RegisterPage() {
  const classes = useStyles();
  const history = useHistory();
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    password2: ''
  });
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [loading, setLoading] = useState(false);
  const [activeStep, setActiveStep] = useState(0);

  const steps = ['Account Details', 'Password Setup', 'Complete'];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const validateForm = () => {
    if (formData.password !== formData.password2) {
      setError('Passwords do not match');
      return false;
    }
    
    if (formData.password.length < 6) {
      setError('Password must be at least 6 characters long');
      return false;
    }
    
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    
    if (!validateForm()) {
      return;
    }
    
    setLoading(true);
    
    try {
      // Send data as JSON instead of form data
      console.log('Attempting to register user:', formData.username, formData.email);
      
      const response = await fetch(`${API_URL}/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username: formData.username,
          email: formData.email,
          password: formData.password
        }),
        credentials: 'include' // Include cookies for session management
      });
      
      // Parse response based on content type
      const contentType = response.headers.get('content-type');
      if (contentType && contentType.includes('application/json')) {
        const data = await response.json();
        
        if (response.ok) {
          setSuccess('Registration successful! You can now log in.');
          // Reset form
          setFormData({
            username: '',
            email: '',
            password: '',
            password2: ''
          });
          
          // Move to final step
          setActiveStep(2);
          
          // Redirect to login after short delay
          setTimeout(() => {
            history.push('/login');
          }, 3000);
        } else {
          setError(data.error || 'Registration failed. Please try again.');
        }
      } else {
        // If response is not JSON, likely a success (Flask may redirect)
        if (response.ok) {
          setSuccess('Registration successful! You can now log in.');
          
          // Move to final step
          setActiveStep(2);
          
          // Redirect to login after short delay
          setTimeout(() => {
            history.push('/login');
          }, 3000);
        } else {
          setError('Registration failed. Please try again.');
        }
      }
    } catch (error) {
      console.error('Error during registration:', error);
      setError('Connection error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleNext = () => {
    // Basic validation for first step
    if (activeStep === 0) {
      if (!formData.username.trim() || !formData.email.trim()) {
        setError('Please fill in all fields');
        return;
      }
      
      // Simple email validation
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(formData.email)) {
        setError('Please enter a valid email address');
        return;
      }
      
      setError('');
    }
    
    setActiveStep((prevStep) => prevStep + 1);
  };
  
  const handleBack = () => {
    setActiveStep((prevStep) => prevStep - 1);
    setError('');
  };

  return (
    <Container maxWidth="sm" className={classes.registerContainer}>
      <Zoom in={true} timeout={800}>
        <Card className={classes.registerCard}>
          <div className={classes.header}>
            <FitnessCenter className={classes.logo} />
            <Typography variant="h4" component="h1" gutterBottom>
              Join AI Fitness Trainer
            </Typography>
            <Typography variant="body1">
              Create your account to start tracking your fitness journey
            </Typography>
          </div>
          
          <CardContent className={classes.form}>
            <Stepper activeStep={activeStep} className={classes.stepper}>
              {steps.map((label) => (
                <Step key={label}>
                  <StepLabel 
                    StepIconProps={{ 
                      classes: { 
                        root: classes.stepIcon,
                        active: classes.active,
                        completed: classes.completed 
                      } 
                    }}
                    className={classes.stepLabel}
                  >
                    {label}
                  </StepLabel>
                </Step>
              ))}
            </Stepper>
            
            {error && (
              <Fade in={!!error}>
                <Alert severity="error" style={{ marginBottom: 16 }}>
                  {error}
                </Alert>
              </Fade>
            )}
            
            {success && (
              <Fade in={!!success}>
                <Alert severity="success" style={{ marginBottom: 16 }}>
                  {success}
                </Alert>
              </Fade>
            )}

            <form onSubmit={handleSubmit}>
              {activeStep === 0 && (
                <Fade in={activeStep === 0}>
                  <div>
                    <TextField
                      fullWidth
                      label="Username"
                      name="username"
                      value={formData.username}
                      onChange={handleChange}
                      margin="normal"
                      variant="outlined"
                      required
                      className={classes.textField}
                      InputProps={{
                        startAdornment: (
                          <AccountCircle style={{ color: '#757575', marginRight: '8px' }} />
                        ),
                      }}
                    />
                    
                    <TextField
                      fullWidth
                      label="Email"
                      name="email"
                      type="email"
                      value={formData.email}
                      onChange={handleChange}
                      margin="normal"
                      variant="outlined"
                      required
                      className={classes.textField}
                      InputProps={{
                        startAdornment: (
                          <Email style={{ color: '#757575', marginRight: '8px' }} />
                        ),
                      }}
                    />
                    
                    <Box mt={3} display="flex" justifyContent="flex-end">
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={handleNext}
                        className={classes.submitButton}
                      >
                        Next
                      </Button>
                    </Box>
                  </div>
                </Fade>
              )}
              
              {activeStep === 1 && (
                <Fade in={activeStep === 1}>
                  <div>
                    <TextField
                      fullWidth
                      label="Password"
                      name="password"
                      type="password"
                      value={formData.password}
                      onChange={handleChange}
                      margin="normal"
                      variant="outlined"
                      required
                      className={classes.textField}
                      helperText="Password must be at least 6 characters long"
                      InputProps={{
                        startAdornment: (
                          <LockOutlined style={{ color: '#757575', marginRight: '8px' }} />
                        ),
                      }}
                    />
                    
                    <TextField
                      fullWidth
                      label="Confirm Password"
                      name="password2"
                      type="password"
                      value={formData.password2}
                      onChange={handleChange}
                      margin="normal"
                      variant="outlined"
                      required
                      className={classes.textField}
                      error={formData.password !== formData.password2 && formData.password2 !== ''}
                      helperText={
                        formData.password !== formData.password2 && formData.password2 !== '' 
                          ? 'Passwords do not match' 
                          : ' '
                      }
                      InputProps={{
                        startAdornment: (
                          <LockOutlined style={{ color: '#757575', marginRight: '8px' }} />
                        ),
                      }}
                    />
                    
                    <Box mt={3} display="flex" justifyContent="space-between">
                      <Button
                        onClick={handleBack}
                        className={classes.submitButton}
                      >
                        Back
                      </Button>
                      <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        disabled={loading}
                        className={classes.submitButton}
                      >
                        {loading ? <CircularProgress size={24} color="inherit" /> : 'Create Account'}
                      </Button>
                    </Box>
                  </div>
                </Fade>
              )}
              
              {activeStep === 2 && (
                <Grow in={activeStep === 2}>
                  <Box textAlign="center" py={3}>
                    <CheckCircle style={{ fontSize: 80, color: '#4caf50', marginBottom: 16 }} />
                    <Typography variant="h5" gutterBottom>
                      Registration Successful!
                    </Typography>
                    <Typography variant="body1" paragraph>
                      Your account has been created successfully.
                    </Typography>
                    <Typography variant="body1">
                      You'll be redirected to the login page shortly...
                    </Typography>
                  </Box>
                </Grow>
              )}
            </form>
            
            {activeStep !== 2 && (
              <>
                <Divider style={{ margin: '24px 0' }} />
                
                <Box textAlign="center">
                  <Typography variant="body1">
                    Already have an account?{' '}
                    <Link to="/login" className={classes.loginLink}>
                      Sign In
                    </Link>
                  </Typography>
                </Box>
              </>
            )}
          </CardContent>
        </Card>
      </Zoom>
    </Container>
  );
}

export default RegisterPage;