import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import {
  Typography,
  Container,
  Paper,
  TextField,
  Button,
  Box,
  CircularProgress,
  Divider,
  Card,
  CardContent,
  Zoom,
  Fade,
  InputAdornment,
  IconButton
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { 
  LockOutlined, 
  AccountCircle, 
  FitnessCenter, 
  Email,
  Visibility,
  VisibilityOff
} from '@material-ui/icons';
import Alert from '../components/Alert';

// API base URL
const API_URL = 'http://localhost:5000';

// Custom styles with hover effects
const useStyles = makeStyles((theme) => ({
  loginContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    minHeight: 'calc(100vh - 184px)', // Account for header and footer
    backgroundColor: '#121212',
    padding: theme.spacing(3, 0),
  },
  loginCard: {
    borderRadius: 12,
    boxShadow: '0 8px 40px rgba(0, 0, 0, 0.5)',
    overflow: 'hidden',
    backgroundColor: '#1e1e1e',
    transition: 'transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out',
    maxWidth: 450,
    width: '100%',
    '&:hover': {
      transform: 'translateY(-8px)',
      boxShadow: '0 16px 70px rgba(0, 0, 0, 0.7)',
    },
  },
  header: {
    padding: theme.spacing(4),
    textAlign: 'center',
  },
  form: {
    padding: theme.spacing(4),
  },
  textField: {
    marginBottom: theme.spacing(2),
    '& .MuiOutlinedInput-root': {
      borderRadius: 8,
      backgroundColor: 'rgba(255, 255, 255, 0.05)',
      transition: 'all 0.3s ease',
      '&:hover fieldset': {
        borderColor: theme.palette.primary.main,
      },
      '&.Mui-focused fieldset': {
        borderColor: theme.palette.primary.main,
        boxShadow: '0 0 0 2px rgba(33, 150, 243, 0.2)',
      },
    },
    '& .MuiInputLabel-root': {
      color: 'rgba(255, 255, 255, 0.7)',
    },
    '& .MuiInputBase-input': {
      color: '#ffffff',
    },
    '& .MuiInputAdornment-root .MuiSvgIcon-root': {
      color: 'rgba(255, 255, 255, 0.5)',
    },
  },
  submitButton: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(2),
    height: 56,
    borderRadius: 28,
    background: '#2196f3',
    boxShadow: '0 4px 10px rgba(33, 150, 243, 0.3)',
    transition: 'all 0.3s ease',
    textTransform: 'none',
    fontWeight: 600,
    fontSize: '1.1rem',
    '&:hover': {
      background: '#1976d2',
      boxShadow: '0 6px 15px rgba(33, 150, 243, 0.4)',
      transform: 'translateY(-2px)',
    },
  },
  signupLink: {
    color: theme.palette.primary.main,
    textDecoration: 'none',
    fontWeight: 500,
    position: 'relative',
    padding: '2px 0',
    transition: 'all 0.3s ease',
    '&:after': {
      content: '""',
      position: 'absolute',
      width: '0%',
      height: '2px',
      bottom: '0',
      left: '50%',
      transform: 'translateX(-50%)',
      backgroundColor: theme.palette.primary.main,
      transition: 'all 0.3s ease',
    },
    '&:hover': {
      color: theme.palette.primary.light,
      '&:after': {
        width: '100%',
      },
    },
  },
  logo: {
    fontSize: 38,
    color: '#2196f3',
    marginRight: theme.spacing(1),
  },
  logoText: {
    fontWeight: 700,
    color: '#ffffff',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: theme.spacing(4),
  },
  divider: {
    backgroundColor: 'rgba(255, 255, 255, 0.1)',
    margin: theme.spacing(3, 0),
  },
  welcomeBack: {
    fontWeight: 700,
    marginBottom: theme.spacing(1),
    color: '#ffffff',
  },
  subtitle: {
    color: 'rgba(255, 255, 255, 0.6)',
    marginBottom: theme.spacing(3),
  }
}));

function LoginPage({ setIsLoggedIn }) {
  const classes = useStyles();
  const history = useHistory();
  const [formData, setFormData] = useState({
    username: '',
    password: ''
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);
    
    try {
      console.log('Attempting to login with:', formData.username);
      
      const response = await fetch(`${API_URL}/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          username: formData.username,
          password: formData.password
        }),
        credentials: 'include' // Important for cookies
      });
      
      const data = await response.json();
      
      if (response.ok) {
        console.log('Login successful');
        setIsLoggedIn(true);
        history.push('/'); // Redirect to home page after login
      } else {
        console.error('Login failed:', data.message);
        setError(data.message || 'Invalid username or password');
      }
    } catch (err) {
      console.error('Login error:', err);
      setError('Connection error. Please check if the server is running.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="sm" className={classes.loginContainer}>
      <Zoom in={true} timeout={800}>
        <Card className={classes.loginCard}>
          <CardContent>
            <Box className={classes.header}>
              <Typography variant="h4" className={classes.logoText}>
                <FitnessCenter className={classes.logo} />
                AIFitTrack
              </Typography>
              
              <Typography variant="h5" className={classes.welcomeBack}>
                Welcome Back!
              </Typography>
              <Typography variant="body1" className={classes.subtitle}>
                Log in to track your exercise progress
              </Typography>
            </Box>
            
            <Box className={classes.form}>
              {error && (
                <Fade in={!!error}>
                  <Alert severity="error" style={{ marginBottom: 16 }}>
                    {error}
                  </Alert>
                </Fade>
              )}

              <form onSubmit={handleSubmit}>
                <TextField
                  fullWidth
                  label="Email"
                  name="username"
                  value={formData.username}
                  onChange={handleChange}
                  variant="outlined"
                  required
                  className={classes.textField}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Email />
                      </InputAdornment>
                    ),
                  }}
                />
                
                <TextField
                  fullWidth
                  label="Password"
                  name="password"
                  type={showPassword ? 'text' : 'password'}
                  value={formData.password}
                  onChange={handleChange}
                  variant="outlined"
                  required
                  className={classes.textField}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <LockOutlined />
                      </InputAdornment>
                    ),
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowPassword}
                          edge="end"
                          style={{ color: 'rgba(255, 255, 255, 0.5)' }}
                        >
                          {showPassword ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                />
                
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="primary"
                  disabled={loading}
                  className={classes.submitButton}
                >
                  {loading ? <CircularProgress size={24} color="inherit" /> : 'Login'}
                </Button>
              </form>
              
              <Divider className={classes.divider} />
              
              <Box textAlign="center">
                <Typography variant="body1" style={{ color: 'rgba(255, 255, 255, 0.7)' }}>
                  Don't have an account?{' '}
                  <Link to="/register" className={classes.signupLink}>
                    Sign Up
                  </Link>
                </Typography>
              </Box>
            </Box>
          </CardContent>
        </Card>
      </Zoom>
    </Container>
  );
}

export default LoginPage; 